<h1 id="nginx-通过域名代理tcp" tabindex="-1"><a class="header-anchor" href="#nginx-通过域名代理tcp" aria-hidden="true">#</a> Nginx 通过域名代理tcp</h1>
<nav class="table-of-contents"><ul><li><RouterLink to="#_1-需要-stream-模块">1.需要 stream 模块</RouterLink></li><li><RouterLink to="#_2-stream-是一个单独的模块">2.stream 是一个单独的模块</RouterLink></li><li><RouterLink to="#修改nginx-conf-添加如下配置即可">修改nginx.conf，添加如下配置即可</RouterLink></li></ul></nav>
<h2 id="_1-需要-stream-模块" tabindex="-1"><a class="header-anchor" href="#_1-需要-stream-模块" aria-hidden="true">#</a> 1.需要 stream 模块</h2>
<p>nginx 添加stream 模块即可</p>
<h2 id="_2-stream-是一个单独的模块" tabindex="-1"><a class="header-anchor" href="#_2-stream-是一个单独的模块" aria-hidden="true">#</a> 2.stream 是一个单独的模块</h2>
<p>不需要放在nginx-http配置中，放在nginx.conf最后即可</p>
<h2 id="修改nginx-conf-添加如下配置即可" tabindex="-1"><a class="header-anchor" href="#修改nginx-conf-添加如下配置即可" aria-hidden="true">#</a> 修改nginx.conf，添加如下配置即可</h2>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>stream {
    map $ssl_preread_server_name $name {
        mysql.test.com mysql;
        redis.test.com redis;
        default https_default_backend;
    }
    upstream mysql{
        server 10.0.0.3:3306;
    }
    upstream redis{
        server 10.0.0.4:6379;
    }
    upstream https_default_backend {
        server 127.0.0.1:443;
    }
    server {
        listen 10.0.0.1:443;
        proxy_pass $name;
        ssl_preread on;
    }
}
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br></div></div><p>需要把map中参数变成域名</p>
